body {
  background-color: #eaeded;
}

.app-header {
  background-color: #282c34;
  min-height: 6vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.container {
    display: flex;
    flex-wrap: wrap;
    padding: 20px 100px 100px 100px;
    overflow-y: scroll;
    max-height: 600px;
}

.checkout {
  overflow-y: scroll;
  max-height: 93vh;
}

.item-card {
    max-width: 180px;
    padding: 18px;
}

.item-card-price {
  color: red;
}

.item-card-btn {
  background: #f0c14b;
  border-color: #a88734 #9c7e31 #846a29;
  width: 180px;
  height: 25px;
}

button, input[type="submit"], input[type="reset"] {
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
  outline: inherit;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #232121e0;;
  color: white;
  justify-content: space-between;
  display: flex;
    padding: 15px;
}

.checkout-btn {
  background-color: blue;
  margin-right: 30px;
  height: 35px;
  width: 100px;
}

.flex {
  display: flex;
}
.checkout-item-card{
justify-content: space-between;
display: flex;
padding: 20px;
border-bottom: 1px solid #d6dada;
border-top: 1px solid #d6dada;
}

.ml-20 {
  margin-left: 20px;
}

.checkout-table {
  background-color: white;
  max-width: 70%;
  padding: 25px;
  margin-left: 35px;
  margin-top: 40px;
}

.add-remove {
  width: 20px;
    font-size: 22px;
    background-color: #cdcdcd;
    margin-left: 10px;
    margin-right: 10px;
}

.icon-separator {
  display: inline-block;
  margin: -2px .67375em 0;
  width: 1px;
  background-color: #ddd;
  line-height: 0;
  height: 14px;
  vertical-align: middle;
}

.delete-btn {
  color: #007185!important;
  cursor: pointer;
}

.proceed-buy {
  justify-content: flex-end;
  display: flex;
  margin-right: 25%;
}

.sub-total {
  display: flex;
  justify-content: flex-end;
  padding: 20px;
}

.proceed-btn {
  padding: 5px;
    background: #f0c14b;
    max-width: 200px;
    text-align: center;
    margin-top: 20px;
}

.item-card-title{
  height: 70px;
  overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 4;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}

.author {
  color: gray;
    font-size: 15px;
}

input{
  margin: 50px 10px 10px 120px;
  height: 25px;
}

select {
  margin-left: 10px;
    height: 25px;
    width: 120px;
}
